.about{
    background-color: #191919;
}
img.about__img{
    border-radius: 10px;
    width: 100%;
    height:500px;
}
h1.about__heading{
    margin-bottom: 10px;
    font-weight:700;
    text-align: start;
    font-family: Montserrat,sans-serif;
    font-size: 60px;
}
button .about .btn .pointer{
    width: 195px;
    height: 55px;
    font-size: 16px;
    transition: .4s ease;
}
.about__button{
    padding: 20px 0px;
}
.bottom__to__top{
    height: 40px;
    width: 40px;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #f9004d;
    border-radius: 50%;
    cursor: pointer;
    display:none;
    align-items: center;
    justify-content: center;
    transition: .4s all ease;
    z-index: 1000;
}
a.bottom__to__top.active{
    display: flex;
}