.home__bg{
    background-image: url('./img/bgimg3.jpg');
    background-color: #010101;
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    height:750px;
}
li.nav__items{
    font-size: 18px;
    padding-bottom: 15px;
}
.home__content{
    padding: 125px 0;
}
h1.home__text{
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 3px;
    color: rgba(198,201,216,.75)!important;
}
h2.home__text{
    font-size: 50px;
    line-height: 54px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
h3.home__text{
    font-size: 46px;
    line-height: 40px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
h4.home__text{
    font-size: 42px;
    line-height: 40px;
    font-weight: 800;
    font-family: Montserrat,sans-serif;
}
.header{
    transition: .4s all ease;
}
.header.active{
    position: fixed;
    z-index: 1000;
    width: 100%;
    padding: 0px 30px;
    background-color: #191919;
}
